/* Add this at the BOTTOM of your global CSS file */

/* ========================================
   FIX: Chromium GIF Flickering (Compositor Bug)
   ======================================== */
   img[src$=".gif"],
   img[src*=".gif"] {
     transform: translateZ(0) !important;
     will-change: transform;
     backface-visibility: hidden;
     -webkit-backface-visibility: hidden;
   }
   
   /* Apply to GIF containers if needed */
   .logo-container,
   .header-gif,
   .gif-wrapper {
     transform: translateZ(0) !important;
     will-change: transform;
   }